.ProductList {
	display: flex;
	flex-direction: column;
	gap: 32px;

	& h2 {
		color: rgb(33, 33, 33);
		font-size: 32px;
		font-weight: 400;
		line-height: 38px;
		letter-spacing: 0px;
		text-align: left;
	}

	.output {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
	}
}
