.AddPhoto {
	display: flex;
	gap: 20px;

	.add_photo {
		display: flex;
		flex-direction: column;
		align-items: center;
		gap: 7px;
		padding: 45px 25px;
		box-sizing: border-box;
		border: 1px solid rgb(33, 33, 33);
		border-radius: 4px;
		cursor: pointer;

		& img {
			width: 16px;
			height: 15px;
			object-fit: cover;
			object-position: center;
		}

		& p {
			color: rgb(33, 33, 33);
			font-size: 12px;
			font-weight: 500;
			line-height: 130%;
			letter-spacing: 0px;
			text-align: left;
		}

		.hidden {
			opacity: 0;
			width: 0;
			height: 0;
			line-height: 0;
			overflow: hidden;
			padding: 0;
			margin: 0;
		}
	}
}
