.SearchInput {
    display: flex;
    gap: 20px;
    align-items: center;
    padding-bottom: 40px;

    .input_field {
        width: 75%;
        display: flex;
        align-items: center;
        justify-content: end;
        position: relative;

        .label_for_search {
            width: 65%;
            border-radius: 25px;
            height: 50px;
            box-shadow: 0px 4px 5px 1px rgba(73, 73, 73, 0.25);
            display: flex;
            align-items: center;
            padding: 0 20px;
            gap: 10px;

            input {
                font-size: 20px;
                font-weight: 400;
                line-height: 130%;
                letter-spacing: 0%;
                text-align: left;
                height: 100%;
                width: 95%;
            }

            .glass {
                top: 17px;
                right: 62%;
            }
        }
    }

    .category_wrapper {
        position: relative;
        width: 400px;
        margin: 0 auto;

        .category {
            text-align: center;
            font-size: 30px;
            font-weight: 500;
            line-height: 130%;
            letter-spacing: 0%;
            cursor: pointer;
            user-select: none;
        }
    }
}